import React , {useState,useEffect} from 'react'
import { toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css'
import './form.css';
toast.configure()
function Cgpa() {
    const [cgpa,setAdd] = useState({sem1:"",
     sem2:"", sem3:"",
     sem4:"", sem6:"",
     sem5:"", sem7:"",
     sem8:"",
      });
    const [sum,setSum] = useState(0);
    useEffect(()=>{
          var sem=0,add=0;
          const sem1=parseInt(cgpa.sem1);
          const sem2=parseInt(cgpa.sem2);
          const sem3=parseInt(cgpa.sem3);
          const sem4=parseInt(cgpa.sem4);
          const sem5=parseInt(cgpa.sem5);
          const sem6=parseInt(cgpa.sem6);
          const sem7=parseInt(cgpa.sem7);
          const sem8=parseInt(cgpa.sem8);
           if(!Number.isNaN(sem1))
           {
             add+=sem1
             sem+=1;
           }if(!Number.isNaN(sem2))
           {
            add+=sem2
             sem+=1;
           }
           if(!Number.isNaN(sem3))
           {
            add+=sem3
             sem+=1;
           }
           if(!Number.isNaN(sem4))
           {
            add+=sem4
             sem+=1;
           }if(!Number.isNaN(sem5))
           {
            add+=sem5
             sem+=1;
           }
           if(!Number.isNaN(sem6))
           {
            add+=sem6
             sem+=1;
           }
           if(!Number.isNaN(sem7))
           {
            add+=sem7
             sem+=1;
           }
           if(!Number.isNaN(sem8))
           {
            add+=sem8
             sem+=1;
           }
          setSum((add/sem)) 
         
    },[cgpa])
    function Answer(e){
            if(Number.isNaN(sum))
            {
              toast.error('ENTER ATLEAST TWO SUBJECTS',{position:toast.POSITION.TOP_CENTER});
              e.preventDefault();
            }
            else{
              toast.success('ðŸ¦„CGPA =  '+sum, {
                position: "top-center",
                autoClose: false,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
            e.preventDefault();
            }
    }
    function refresh(e)
    {
      setAdd({sem1:"",sem2:"",sem3:"",sem4:"",sem5:"",sem6:"",sem7:"",sem8:""})
     
      e.preventDefault();
    }
   
    return (
        <form>
           <h1>CGPA CALCULATOR</h1>
           <h5>Enter atleast two semester GPA</h5>
    <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 1st GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem1} 
     onChange={e =>setAdd({...cgpa,sem1:e.target.value})} />   
    <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 2nd GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem2} 
     onChange={e =>setAdd({...cgpa,sem2:e.target.value})} />   
      <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 3rd GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem3} 
     onChange={e =>setAdd({...cgpa,sem3:e.target.value})} />
     <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 4th GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem4} 
     onChange={e =>setAdd({...cgpa,sem4:e.target.value})} />          
    <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 5th GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem5} 
     onChange={e =>setAdd({...cgpa,sem5:e.target.value})} />   
    <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 6th GPA</label>
    <input type="text" className="form-control " id="exampleInputEmail1"
     value={cgpa.sem6} 
     onChange={e =>setAdd({...cgpa,sem6:e.target.value})} />             
      <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 7th GPA</label>
    <input type="text" className="form-control" id="exampleInputEmail1"
     value={cgpa.sem7} 
     onChange={e =>setAdd({...cgpa,sem7:e.target.value})} />                     
         <label forhtml="exampleInputEmail1" class="form-label my-2">SEM 8th GPA</label>
    <input type="text" className="form-control" id="exampleInputEmail1"
     value={cgpa.sem8} 
     onChange={e =>setAdd({...cgpa,sem8:e.target.value})} />             
                <button onClick={Answer} className="btn btn-dark mx-auto my-3">Answer</button> 
              <button onClick={refresh} className="btn btn-primary mx-3">Reset</button>
       {}
       </form>
    )
}

export default Cgpa
//  value={cgpa.sem1} 
// onChange={e =>setAdd({...cgpa,sem1:e.target.value})}